.loader {
  display: flex;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 15px;
  height: 15px;
  color: #fff;
  background: currentColor;
  border-radius: 50%;
  margin: 0 5px;
  animation: left 1s ease-in-out infinite;
  display: inline-block;
  position: relative;
  box-shadow: 50px 0, -50px 0;
}

.loader:after {
  color: #ff3d00;
  animation: right 1.1s ease-in-out infinite;
}

@keyframes right {
  0%, 100% {
    transform: translateY(-10px);
  }

  50% {
    transform: translateY(10px);
  }
}

@keyframes left {
  0%, 100% {
    transform: translateY(10px);
  }

  50% {
    transform: translateY(-10px);
  }
}

